<template>
  <v-app>
    <snackbar />
    <router-view></router-view>
    <div id="app-prompt" class="app-prompt white elevation-1">
      <div class="container">
        <v-row>
          <v-col class="d-sm-none pa-0" align="right" cols="12" @click="removerBanner">
            <v-btn text>
              <v-icon>close</v-icon>
            </v-btn>
          </v-col>
          <v-col cols="auto" class="py-0">
            <img src="/icone-72x72.png" class="elevation-2 btnIcon" />
          </v-col>
          <v-col class="py-0 content-prompt auto">
            <strong>Meu Barbeiro</strong>
            <small class="d-block">
              Deseja instalar o meu barbeiro na home do seu dispositivo?
            </small>
            <v-btn id="app-prompt-btn" class="success btnAddIcon py-3 px-6" height="auto">
              Adicionar
            </v-btn>
          </v-col>
          <v-spacer></v-spacer>
          <v-col class="pa-0 d-none d-sm-inline" cols="auto" @click="removerBanner">
            <v-btn text>
              <v-icon>close</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </div>
    </div>
  </v-app>
</template>

<script>
import Snackbar from '@/components/globais/snackbar/Snackbar'

export default {
  name: 'App',
  components: { Snackbar },
  data: () => ({
    deferredPrompt: null
  }),
  created () {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/sw.js', { scope: '/' })
    }
  },
  mounted () {
    if ('serviceWorker' in navigator && !localStorage.recuseInstallApp) {
      let deferredPrompt
      let prompt = window.document.querySelector('#app-prompt')
      let btnInstallIcon = window.document.querySelector('#app-prompt-btn')
      
      window.addEventListener('beforeinstallprompt', function (e) {
        // Prevent the mini-infobar from appearing on mobile
        e.preventDefault()
        // Stash the event so it can be triggered later.
        deferredPrompt = e
        // Update UI notify the user they can install the PWA
        setTimeout(() => {
          prompt.classList.add('active')
        }, 3000)
      })

      btnInstallIcon.addEventListener('click', function () {
        // Hide the app provided install promotion
        prompt.classList.remove('active')
        // Show the install prompt
        deferredPrompt.prompt()
      })
    }
  },
  methods: {
    removerBanner () {
      let btnInstallIcon = window.document.querySelector('#app-prompt')
      btnInstallIcon.classList.remove('active')
      localStorage.recuseInstallApp = true
    }
  }
}
</script>

<style lang="scss" scoped>
.app-prompt {
  font-size: 1.5rem;
  position: fixed;
  bottom: -165px;
  width: 100%;
  padding: 15px 0;
  transition: 1s all;
  &.active {
    bottom: 56px;
    transition: 1s all;
  }
	.btnIcon { cursor: pointer; }
  .btnAddIcon { margin-top: 15px; }
	small { font-size: 1rem; }
}
.content-prompt { min-width: 450px; }

@media all and (max-width: 599px) {
  .app-prompt { padding: 0; bottom: -230px; }
  .content-prompt { padding-left: 0; min-width: calc(100% - 100px); }
}
</style>
